.popup__title {
    color: black;
    font-size: 24px;
    line-height: 29px;
    font-weight: 900;
    margin: 0 0 calc(54px - 10px) 0;
}

@media screen and (max-width: 540px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin: 0 22px 75px 22px;
    }
}
